.topMenu {
  padding: 0 50px;
  box-shadow: 0 2px 20px 0 rgba(0, 0, 0, 0.36), 0 2px 4px 0 rgba(0, 0, 0, 0.15);
  background: rgba(255, 255, 255, 0.05);
